#home {
	padding: 20px;
	ul {
		flex-wrap: wrap;
		min-height: 100%;
	}
	li {
		width: 216px;
		height: 120px;
		border: 1px solid #dfdfdf;
		font-size: 26px;
		text-align: center;
		line-height: 120px;
		margin: 0 20px 20px 0;
		&:nth-child(5n) {
			margin-right: 0;
		}
		a {
			display: block;
			color: #333;
		}
	}
}